import Cards from "./../../Components/Cards/Cards";
import { useDispatch, useSelector } from "react-redux";
import { useEffect, useState } from "react"; // Agrega "useState"
import { getPokemons, reset_search } from "../../redux/actions";
import style from "./Home.css";

const Home = () => {
  let pokemons = [];
  const dispatch = useDispatch();
  const allPokemons = useSelector((state) => state.allPokemons);
  let pokeName = useSelector((state) => state.pokeName);
  const pokXpage = 12;
  if (pokeName[0]) {
    pokemons = pokeName;
  } else {
    pokemons = allPokemons;
  }

  useEffect(() => {
    dispatch(getPokemons());
  }, [dispatch, pokeName]);

  const handleMostrar = () => {
    dispatch(reset_search());
  };

  const totPok = pokemons.length;

  const [currentPage, setCurrentPage] = useState(1); // Estado para la página actual
  const indexOfLastPokemon = currentPage * pokXpage;
  const indexOfFirstPokemon = indexOfLastPokemon - pokXpage;
  const currentPokemons = pokemons.slice(
    indexOfFirstPokemon,
    indexOfLastPokemon
  );

  const pageNumbers = [];
  for (let i = 1; i <= Math.ceil(totPok / pokXpage); i++) {
    pageNumbers.push(i);
  }

  const handlePageClick = (pageNumber) => {
    if (pageNumber === "prev" && currentPage > 1) {
      setCurrentPage((prevPage) => prevPage - 1);
    } else if (pageNumber === "next") {
      setCurrentPage((prevPage) => prevPage + 1);
    } else if (typeof pageNumber === "number") {
      setCurrentPage(pageNumber);
    }
  };

  return (
    <div className={style.Home}>
      <div>
        <Cards pokemons={currentPokemons} />
        {pokemons.length === 1 ? (
          <button onClick={handleMostrar}>Mostrar todos</button>
        ) : (
          <div></div>
        )}
      </div>
      <div className={style.Paginado}>
        {currentPage !== 1 && (
          <button key="prev" onClick={() => handlePageClick("prev")}>
            Prev
          </button>
        )}
        {pageNumbers.map((number) => (
          <button key={number} onClick={() => handlePageClick(number)}>
            {number}
          </button>
        ))}
        {currentPage !== pageNumbers.length && (
          <button key="next" onClick={() => handlePageClick("next")}>
            Next
          </button>
        )}
      </div>
    </div>
  );
};

export default Home;
