import { useDispatch, useSelector } from "react-redux";
import { useEffect, useState } from "react";
import axios from "axios";
import { filter_type } from "../../redux/actions";
const URL = `http://localhost:3001/gettypes`;

const Filter = () => {
  const dispatch = useDispatch();
  const filtered = useSelector((state) => state.filtered);
  const [types, setTypes] = useState([]);

  useEffect(() => {
    const getTypes = async () => {
      try {
        const { data } = await axios.get(URL);
        setTypes(data);
      } catch (error) {
        console.log(error.message);
      }
    };
    getTypes();
  }, []);

  let typesSt = [];
  for (let i = 0; i < types.length; i++) {
    typesSt.push(types[i].name);
  }

  const handleSelect = (event) => {
    const selectedType = event.target.value;
    dispatch(filter_type(selectedType, filtered)); // Pasar el estado "filtered" a la acción
  };

  return (
    <div>
      <label htmlFor="">Filtro por tipo: </label>
      <select id="types" onChange={handleSelect}>
        {typesSt.map((type, i) => (
          <option key={i} value={type}>
            {type}
          </option>
        ))}
      </select>
    </div>
  );
};

export default Filter;
