import {
  GET_POKEMONS,
  SEARCH_POKEMON,
  FILTER_TYPE,
  RESET_SEARCH,
} from "./action-types";

const initialState = {
  allPokemons: [],
  pokeName: [],
  filtered: [],
  currentPage: 1,
};

const reducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case GET_POKEMONS:
      return {
        ...state,
        allPokemons: payload,
      };
    case SEARCH_POKEMON:
      return {
        ...state,
        pokeName: payload,
      };
    case RESET_SEARCH:
      return {
        ...state,
        pokeName: [],
      };

    case FILTER_TYPE:
      return {
        ...state,
        filtered: payload,
      };
    default:
      return state;
  }
};

export default reducer;
