import {
  GET_POKEMONS,
  SEARCH_POKEMON,
  FILTER_TYPE,
  RESET_SEARCH,
} from "./action-types";
import axios from "axios";

export const getPokemons = () => {
  const URL = "http://localhost:3001/pokemons";
  return async (dispatch) => {
    try {
      const { data } = await axios.get(URL);
      if (!data.length) throw Error("No hay pokemones");
      else {
        return dispatch({
          type: GET_POKEMONS,
          payload: data,
        });
      }
    } catch (error) {
      return error.message;
    }
  };
};
export const search_pokemon = (name) => {
  return async (dispatch) => {
    const URL = "http://localhost:3001/pokemons";
    try {
      const { data } = await axios.get(`${URL}?name=${name}`);
      if (data.name) {
        return dispatch({
          type: SEARCH_POKEMON,
          payload: [data],
        });
      } else {
        throw Error("No hay pokemones con ese nombre");
      }
    } catch (error) {
      return error.message;
    }
  };
};

export const reset_search = (dispatch) => {
  return dispatch({
    type: RESET_SEARCH,
  });
};

export const filter_type = (type, state) => {
  return (dispatch) => {
    try {
      if (state && state.length) {
        // Verifica si state no es nulo y tiene elementos
        const pokemonsFiltrados = state.filter((pokemon) => {
          return pokemon.tipo === type;
        });
        return dispatch({
          type: FILTER_TYPE,
          payload: pokemonsFiltrados,
        });
      } else {
        throw new Error("No hay pokemones para filtrar");
      }
    } catch (error) {
      return error.message;
    }
  };
};
